<template>
  <div id="carrito">
    <h3 class="my-3">Productos en el Carrito</h3>

    <p v-if="productosEnCarrito == 0">Aún no tienes productos en tu carrito.</p>

    <b-list-group>
    <b-list-group-item v-for="({imagen, nombre, cantidad, precio}, i) in carrito" :key="i">
      <b-img height="150px" :src="imagen" alt="Imagen Producto"></b-img>
      {{ nombre }} | Cantidad:
      <b-button size="sm" variant="dark" @click="disminuirCantidad(i)"> - </b-button> {{ cantidad
      }} <b-button size="sm" variant="dark" @click="aumentarCantidad(i)"> + </b-button> | Total por producto:
      ${{ precio * cantidad }}
    </b-list-group-item>
    </b-list-group>
    <hr />

    <h3 class="my-3">Total a Pagar: ${{ totalPago }}</h3>
    <b-button variant="dark" @click="algunDia">Ir a Pagar</b-button>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";

export default {
  name: "Home",
  computed: {
    ...mapState(["carrito", "totalPago"]),
    ...mapGetters(["productosEnCarrito"])
  },
  methods: {
    ...mapActions([
      "eliminar_Carrito",
      "disminuir_Cantidad",
      "aumentar_Cantidad",
    ]),
    disminuirCantidad(i) {
      if (this.carrito[i].cantidad <= 1) {
        let confirmacion = confirm(
          "¿Quieres eliminar este producto del carrito?"
        );
        confirmacion
          ? this.eliminar_Carrito(i)
          : alert("No se eliminó el producto.");
      } else {
        this.disminuir_Cantidad(i);
      }
    },
    aumentarCantidad(i) {
      this.aumentar_Cantidad(i);
    },
    algunDia() {
      alert("Algún día funcionará...");
    },
  },
};
</script>

<style scoped>
  b-list-group-item img {
    height: 100px;
    width: auto;
  }
</style>
