<template>
  <div class="home">
    <div id="buscador">
      <h3 class="my-3">Productos Disponibles</h3>
      <label class="mx-2">
        Buscar por:
        <select name="seleccionarCriterio" v-model="criterioSeleccionado">
          <option value="nombre">Nombre</option>
          <option value="categoria">Categoría</option>
          <option value="color">Color</option>
          <option value="oferta">En oferta</option>
        </select></label
      >
      <label v-if="criterioSeleccionado != 'oferta' && criterioSeleccionado != 'categoria'">
        Palabra para búsqueda: <input type="text" v-model="busqueda" />
      </label>
        <label v-if="criterioSeleccionado == 'categoria'">
          <select v-model="busqueda">
          <option value="Juegos">Juegos</option>
          <option value="Joysticks">Joysticks</option>
          <option value="Amiibos">Amiibos</option>
          <option value="Articulos">Articulos</option>
          <option value="Juguetes">Juguetes</option>
        </select>
      </label>
      <p v-if="criterioSeleccionado == 'oferta'">Productos en oferta:</p>
    </div>
    <div id="productosEnVenta">
      <div
        v-for="({ imagen, nombre, precio, id }, i) in productosPorFiltro"
        :key="i"
      >
        <b-card
          :title="nombre"
          :img-src="imagen"
          img-alt="Image"
          img-top
          tag="article"
          style="max-width: 20rem"
          class="card m-4"
        >
          <b-card-text> ${{ precio }} </b-card-text>
          <b-button variant="dark" @click="addToCart(i, id)">Añadir al carrito</b-button>
        </b-card>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "Home",
  data() {
    return {
      criterioSeleccionado: "nombre",
      busqueda: "",
    };
  },
  methods: {
    ...mapActions(["add_To_Cart"]),
    addToCart(i, id) {
      // let verificador = this.carrito.filter(p => {
      //   p.id === id
      // });
      // if (verificador.lenght != 0){
      //   alert("Este producto ya se encuentra en el carrito.")
      // } else {
      const numero = prompt(
        "Indica la cantidad de este producto que quieres agregar al carrito"
      );
      this.add_To_Cart([i, numero]);
      // }
    },
  },
  computed: {
    ...mapState(["productos", "carrito"]),
    productosPorFiltro() {
      const { criterioSeleccionado, busqueda } = this;
      if (criterioSeleccionado == "oferta") {
        return this.productos.filter((p) => {
          return p.oferta == true;
        });
      } else {
        return this.productos.filter((p) => {
          return p[criterioSeleccionado]
            .toLowerCase()
            .includes(busqueda.toLowerCase());
        });
      }
    },
  },
};
</script>

<style scoped>
  #productosEnVenta {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .card {
    height: 400px;
  }
  .card img {
    width: 200px;
    margin: auto
  }
</style>
