<template>
<div id="productos">
  <div id="nuevoProducto" class="my-5">
    <h3 class="my-3">Ingresar Nuevo Producto</h3>
    <form id="formularioNuevoProducto">
      <label>
        Id: <input type="text" placeholder="0001" v-model="producto.id" />
      </label>
      <label>
        Categoría:
        <select v-model="producto.categoria">
          <option
            v-for="(categoria, i) in producto.categorias"
            :value="categoria"
            :key="i"
          >
            {{ categoria }}
          </option>
        </select></label
      >
      <label
        >Nombre:
        <input placeholder="Ej: Kilo Manzanas" v-model="producto.nombre"
      /></label>
      <label
        >Imagen: <input placeholder="Url de imagen" v-model="producto.imagen"
      /></label>
      <label
        >Precio: <input placeholder="Ej: 12000" v-model="producto.precio"
      /></label>
      <label
        >Color:
        <select v-model="producto.color">
          <option v-for="(color, i) in producto.colors" :value="color" :key="i">
            {{ color }}
          </option>
        </select></label
      >
      <label
        >En oferta: <input type="checkbox" v-model="producto.oferta"
      /></label>
      <label v-show="producto.oferta"
        >% descuento:<input placeholder="Ej: 30" v-model="producto.descuento"
      /></label>
      <b-button variant="dark" class="mx-5" @click="addProduct">Agregar Producto</b-button>
    </form>
  </div>
  <div id="Gestion Productos" class="my-5">
    <h3 class="my-3">Gestión de Productos Ingresados</h3>
    <b-list-group>
      <b-list-group-item v-for="({ id, nombre, categoria, precio }, i) in productos" :key="i">
        Id:{{ id }} | {{ nombre }} | {{ categoria }} | ${{ precio }}
        <b-button variant="dark" class="m-1" @click="eliminarProducto(i)">Eliminar</b-button>
      </b-list-group-item>
    </b-list-group>
  </div>
</div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  name: "Nuevo-Producto",
  data() {
    return {
      producto: {
        id: "",
        categorias: ["Juegos", "Joysticks", "Amiibos", "Articulos", "Juguetes"],
        nombre: "",
        imagen: "",
        precio: "",
        colors: ["verde", "amarillo", "rojo"],
        oferta: false,
        descuento: "",
        cantidad: 0,
      },
    };
  },
  methods: {
    ...mapActions(["add_Product", "eliminar_Producto"]),
    addProduct() {
      const { producto } = this;
      if (
        producto.id == "" ||
        producto.categoria == "" ||
        producto.nombre == "" ||
        producto.imagen == "" ||
        producto.precio == "" ||
        producto.color == ""
      ) {
        return alert("Debes completar todos los campos.");
      } else {
        producto.precio = Number(producto.precio);
        this.add_Product(producto);
        alert("Producto añadido a catálogo.");
      }
    },
    eliminarProducto(i) {
      this.eliminar_Producto(i);
    },
  },
  computed: {
    ...mapState(["productos"]),
  },
};
</script>

<style scoped>
#formularioNuevoProducto {
  display: flex;
  flex-direction: column;
  text-align: left;
  width: fit-content;
  margin: auto;
}
#nuevoProducto {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}
</style>